.social-links {
  width: 150px;
  margin: 0 auto;

  @media (min-width: 768px) {
    margin: 0;
  }
}

.social-links__list {
  display: flex;
  justify-content: space-between;
  gap: 15px;
  width: 150px;
  margin-bottom: 25px;

  @media (min-width: 768px) {
    margin: 0;
  }
}

.social-links__item {
  width: 40px;
  height: 40px;
}

.social-links__link {
  width: 100%;
  height: 100%;
  display: block;
  display: flex;
  justify-content: center;
  align-items: center;
}