.popup {
  display: none;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(3, 16, 27, 0.7);
  z-index: 11;
  justify-content: center;
  align-items: center;

  &--is-open {
    display: flex;
  }
}

.popup__wrapper {
  position: relative;
  width: 500px;
  height: 610px;
  background-color: $base-blue;
  border-radius: 3px;
  padding: 40px;
}

.popup__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: $color-default-white;
  font-size: 30px;
  margin-bottom: 15px;
  font-weight: 400;
}

.popup__description {
  color: $color-default-white;
  font-size: 16px;
  line-height: 21px;
  margin-bottom: 40px;
}

.popup__button-close {
  position: absolute;
  width: 40px;
  height: 40px;
  top: 15px;
  right: 15px;
  background-color: inherit;
  background-image: url("../../img/sprite/icon-close-popup.svg");
  background-repeat: no-repeat;
  background-position: center;
}