@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed.woff") format("woff");
  src: url("../fonts/roboto-condensed.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-700.woff") format("woff");
  src: url("../fonts/roboto-condensed-700.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/pt-mono.woff") format("woff");
  src: url("../fonts/pt-mono.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto.woff") format("woff");
  src: url("../fonts/roboto.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
  border: none;
  outline: none;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: "PT Mono", "Monaco", monospace;
  color: #041827;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.logo-block {
  padding-top: 15px;
  padding-bottom: 20px;
}
@media (min-width: 1024px) {
  .logo-block {
    padding-top: 30px;
    padding-bottom: 35px;
  }
}
@media (min-width: 768px) {
  .logo-block {
    padding-top: 20px;
    padding-bottom: 25px;
  }
}

.logo-block__link {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 15px;
}

.logo-block__company-name {
  display: none;
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  color: #F2F5F6;
  font-size: 25px;
  line-height: 29px;
  font-weight: 700;
}
.logo-block__company-name--footer {
  display: block;
}
@media (min-width: 768px) {
  .logo-block__company-name {
    display: block;
  }
}

.logo-block__description {
  display: none;
  font-family: "Roboto", "Arial", sans-serif;
  color: rgba(255, 255, 255, 0.7);
  font-size: 12px;
  line-height: 14px;
}
.logo-block__description--footer {
  display: block;
}
@media (min-width: 768px) {
  .logo-block__description {
    display: block;
  }
}

.logo-block__logo {
  width: 55px;
  height: 55px;
}
@media (max-width: 767px) {
  .logo-block__logo {
    width: 35px;
    height: 35px;
  }
}

@media (max-width: 767px) {
  .logo-block__logo--footer {
    width: 55px;
    height: 55px;
  }
}

.logo-block--footer {
  width: 215px;
  margin: 0 auto;
  margin-bottom: 15px;
  padding: 0;
}
@media (min-width: 768px) {
  .logo-block--footer {
    padding: 0;
    margin: 0;
  }
}
@media (min-width: 1024px) {
  .logo-block--footer {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
  }
}

.header {
  position: absolute;
  top: 0;
  width: 100%;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  z-index: 10;
}
@media (min-width: 1024px) {
  .header {
    position: fixed;
  }
}

.header__logo-block {
  margin-right: 38.7%;
}
@media (min-width: 1024px) {
  .header__logo-block {
    margin-right: 8.3%;
  }
}
@media (min-width: 768px) {
  .header__logo-block {
    margin-right: 43.5%;
  }
}

.header__contacts-block {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media (min-width: 1024px) {
  .header__contacts-block {
    margin-right: 7.3%;
    gap: 30px;
  }
}

.header__contact-link {
  display: flex;
  align-items: center;
  padding: 10px;
}
@media (min-width: 1024px) {
  .header__contact-link {
    width: 225px;
    gap: 15px;
  }
  .header__contact-link:nth-child(2) {
    width: 228px;
  }
}

.header__link-text {
  font-size: 16px;
  line-height: 18px;
  color: #F2F5F6;
  display: none;
}
@media (min-width: 1024px) {
  .header__link-text {
    display: block;
  }
}

.header__open-popup-btn {
  display: none;
}
@media (min-width: 1024px) {
  .header__open-popup-btn {
    display: block;
    background-color: inherit;
    font-family: "Roboto Condensed", "Arial", "sans-serif";
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: #F2F5F6;
    border: 1px solid #F2F5F6;
    padding: 10px 15px;
    border-radius: 3px;
  }
}

.main-description {
  width: 100%;
  background-color: #000;
  background-image: url("../../img/main-block-background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 305px 37.8% 280px 24.7%;
  background-position: center;
}
@media (max-width: 1023px) {
  .main-description {
    padding: 180px 50px 205px 50px;
    background-position: -173px;
  }
}
@media (max-width: 767px) {
  .main-description {
    background-position: -254px;
    padding: 150px 25px 190px 25px;
  }
}

.main-description__title {
  margin: 0;
  margin-bottom: 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  color: #F2F5F6;
  text-transform: uppercase;
  letter-spacing: 0.07em;
  width: 800px;
  font-size: 80px;
  line-height: 94px;
}
@media (max-width: 1023px) {
  .main-description__title {
    font-size: 64px;
    line-height: 75px;
    width: auto;
  }
}
@media (max-width: 767px) {
  .main-description__title {
    font-size: 36px;
    line-height: 42px;
    width: auto;
  }
}

.main-description__description {
  font-size: 22px;
  line-height: 34px;
  margin-bottom: 50px;
  color: #F2F5F6;
}
@media (max-width: 767px) {
  .main-description__description {
    font-size: 14px;
    line-height: 22px;
  }
}

.main-description__button {
  padding: 15px 0;
  width: 420px;
  background-color: #00B2FF;
}
.main-description__button::after {
  content: "Получить беслпатную консультацию";
  color: #F2F5F6;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
}
@media (max-width: 1023px) {
  .main-description__button {
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .main-description__button {
    padding: 10px 0;
    width: 270px;
    border-radius: 3px;
    margin-left: 0;
  }
  .main-description__button::after {
    content: "Беслпатная консультация";
    font-size: 14px;
    line-height: 16px;
  }
}

.benefits-block {
  width: 100%;
  padding: 50px 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .benefits-block {
    padding: 80px 50px;
  }
}
@media (min-width: 1024px) {
  .benefits-block {
    padding: 120px 0;
  }
}

.benefits-block__list {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .benefits-block__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 30px;
  }
}
@media (min-width: 1024px) {
  .benefits-block__list {
    max-width: 62.5%;
    margin: 0 auto;
  }
}

.benefit-item {
  position: relative;
  width: 100%;
  padding: 30px 30px 35px 30px;
  border: 1px solid #EBEBEB;
}
.benefit-item::after {
  content: "";
  position: absolute;
  left: -1px;
  top: -1px;
  height: calc(100% + 2px);
  width: 5px;
  background-color: #00B2FF;
}
.benefit-item:hover {
  background-color: #00B2FF;
  border-color: #00B2FF;
  color: #fff;
}
.benefit-item:hover h2 {
  color: #fff;
}
.benefit-item:hover svg g {
  fill: #fff;
}
@media (min-width: 768px) {
  .benefit-item {
    min-width: 100%;
    height: 100%;
    padding: 40px 45px 35px 45px;
  }
}
@media (min-width: 1024px) {
  .benefit-item {
    min-height: 300px;
  }
}

.benefit-item__icon {
  width: 35px;
  height: 35px;
}
@media (max-width: 767px) {
  .benefit-item__icon {
    width: 30px;
    height: 30px;
  }
}

.benefit-item__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  overflow-wrap: break-word;
  width: 100%;
  color: #00B2FF;
  font-size: 20px;
  line-height: 28px;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .benefit-item__title {
    font-size: 24px;
    line-height: 34px;
  }
}

.benefit-item__icon {
  margin-bottom: 20px;
}

.benefit-item__description {
  font-size: 14px;
  line-height: 19px;
}
@media (min-width: 768px) {
  .benefit-item__description {
    font-size: 16px;
    line-height: 21px;
  }
}

