.about-company__button-more {
  width: 180px;
  padding: 14px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  background-color: inherit;
  color: #00B2FF;
  border: 1px solid #00B2FF;
  border-radius: 3px;
